import { searchService } from '@services';
import { BaseFacetComponent } from '../base-facet.component';
import defaultHtml from './numeric-range-facet.component.hbs';

export class NumericRangeFacetComponent extends BaseFacetComponent {
    protected customTemplatePropertyName = 'numericRangeFacet';
    protected defaultHtml = defaultHtml;

    protected getContentHtml(template: HandlebarsTemplateDelegate): string {
        if (!this.data) {
            return '';
        }

        const minValue =
            this.data!.rangeStart !== undefined || this.data!.rangeMin !== undefined ? Math.floor((this.data!.rangeStart || this.data!.rangeMin)!) : undefined;
        const maxValue =
            this.data!.rangeEnd !== undefined || this.data!.rangeMax !== undefined ? Math.ceil((this.data!.rangeEnd || this.data!.rangeMax)!) : undefined;

        const html = template({
            id: this.data.id,
            field: this.data.field,
            minValue: minValue,
            maxValue: maxValue
        });

        return html;
    }

    protected bindChildElements(): void {
        this.shadowRoot?.querySelectorAll('[hawksearch-min]').forEach((e) => {
            e.addEventListener('blur', (event: Event) => {
                const element = event.currentTarget as HTMLInputElement;
                const value = parseFloat(element.value) ?? undefined;

                if ((!this.data!.rangeStart || value != this.data!.rangeStart) && (!this.data!.rangeEnd || value <= this.data!.rangeEnd)) {
                    this.data!.rangeStart = value;

                    this.setFacetValue(value, this.data!.rangeEnd);
                } else if (value === undefined) {
                    this.data!.rangeStart = undefined;
                }
            });
        });

        this.shadowRoot?.querySelectorAll('[hawksearch-max]').forEach((e) => {
            e.addEventListener('blur', (event: Event) => {
                const element = event.currentTarget as HTMLInputElement;
                const value = parseFloat(element.value) ?? undefined;

                if ((!this.data!.rangeEnd || value != this.data!.rangeEnd) && (!this.data!.rangeStart || value >= this.data!.rangeStart)) {
                    this.data!.rangeEnd = value;

                    this.setFacetValue(this.data!.rangeStart, value);
                } else if (value === undefined) {
                    this.data!.rangeEnd = undefined;
                }
            });
        });
    }

    private updateInputElements(min: number | undefined, max: number | undefined): void {
        const minElement = this.shadowRoot?.querySelector('[hawksearch-min]') as HTMLInputElement | null;
        const maxElement = this.shadowRoot?.querySelector('[hawksearch-max]') as HTMLInputElement | null;

        if (minElement) {
            minElement.value = min?.toString() || '';
        }

        if (maxElement) {
            maxElement.value = max?.toString() || '';
        }
    }

    private setFacetValue(min: number | undefined, max: number | undefined): void {
        this.updateInputElements(min, max);

        searchService.setFacetValue(this.data!.field!, `${min ?? ''},${max ?? ''}`);
    }
}
